<template>
  <header class="b-layout__footer" :class="classes" ref="footer">
    <slot></slot>
  </header>
</template>

<script>
export default {
  name: "bLayoutFooter",
  data() {
    return {
      classes: ""
    };
  },
  mounted() {
    this.classes =
      this.$refs.footer.innerHTML !== "" ? "b-layout__footer--exist" : "";
  }
};
</script>

<style>
.b-layout__footer {
  grid-area: footer;
}
.b-layout__footer--exist {
  height: 40px;
  padding: 16px 32px;
  box-shadow: 0 -1px 2px rgba(0, 0, 0, 0.1);
}
</style>
