<template>
  <header class="b-layout__header" :class="classes" ref="header">
    <slot></slot>
  </header>
</template>

<script>
export default {
  name: "bLayoutHeader",
  data() {
    return {
      classes: ""
    };
  },
  mounted() {
    this.classes =
      this.$refs.header.innerHTML !== "" ? "b-layout__header--exist" : "";
  }
};
</script>

<style>
.b-layout__header {
  grid-area: header;
}
.b-layout__header--exist {
  height: 40px;
  padding: 16px 32px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}
</style>
